import http from "../utils/http";
// let url = "https://xs2773go.goer.cloud";
let url = ""
export function getUserInfo(params) {
  return http.post(url + "/api/userInfo", params);
}
export function getOrderList(params) {
  return http.post(url + "/api/orderList", params);
}
export function getWithdrawList(params) {
  return http.post(url + "/api/withdrawList", params);
}
export function getIntroIncomeList(params) {
  return http.post(url + "/api/introList", params);
}

